.min-h-screen{min-height:100vh}.bg-gray-50{background:#f9fafb}
.max-w-7xl{max-width:80rem}.mx-auto{margin-left:auto;margin-right:auto}
.grid{display:grid}.gap-6{gap:1.5rem}.gap-4{gap:1rem}.flex{display:flex}
.items-center{align-items:center}.justify-between{justify-content:space-between}
.p-6{padding:1.5rem}.p-4{padding:1rem}.px-2{padding-left:.5rem;padding-right:.5rem}
.py-1{padding-top:.25rem;padding-bottom:.25rem}.rounded-2xl{border-radius:1rem}
.rounded-xl{border-radius:.75rem}.rounded{border-radius:.25rem}.rounded-full{border-radius:9999px}
.shadow{box-shadow:0 1px 3px rgba(0,0,0,.1),0 1px 2px rgba(0,0,0,.06)}
.border{border:1px solid #e5e7eb}.border-l{border-left:1px solid #e5e7eb}
.text-sm{font-size:.875rem}.text-xs{font-size:.75rem}.text-2xl{font-size:1.5rem}
.font-bold{font-weight:700}.font-semibold{font-weight:600}.text-gray-500{color:#6b7280}
.text-gray-600{color:#4b5563}.text-gray-700{color:#374151}.text-white{color:#fff}
.bg-white{background:#fff}.bg-blue-600{background:#2563eb}.bg-yellow-200{background:#fef08a}
.bg-indigo-200{background:#c7d2fe}.w-full{width:100%}.w-24{width:6rem}.w-12{width:3rem}
.w-16{width:4rem}.w-20{width:5rem}.ml-auto{margin-left:auto}.inline-flex{display:inline-flex}
.overflow-hidden{overflow:hidden}.sm\:grid-cols-2{grid-template-columns:repeat(2,minmax(0,1fr))}
.lg\:grid-cols-3{grid-template-columns:repeat(3,minmax(0,1fr))}
.md\:grid-cols-2{grid-template-columns:repeat(2,minmax(0,1fr))}
button{cursor:pointer}
